 .bg {
   animation: slide 3s ease-in-out infinite alternate;
   background-image: linear-gradient(-20deg, var(--Blue) 50%, var(--Black) 50%);
   bottom: 0;
   left: -100%;
   opacity: .5;
   position: fixed;
   right: -50%;
   top: 0;
   z-index: -1;
 }

 .bg2 {
   animation-direction: alternate-reverse;
   animation-duration: 4s;
 }

 .bg3 {
   animation-duration: 5s;
 }

