 @keyframes slide {
   0% {
     transform: translateX(-25%);
   }

   100% {
     transform: translateX(25%);
   }
 }

 .bannerheading {
   margin: 0 auto;
   left: 0;
   right: 0;
   height: 300px;
   max-width: 600px;
 }

 .bannerheading button {
   /* background-color: var(--Black);
    border: 2px solid var(--SkyBlue);
    font-size: 45px;
    color: var(--Cream);
    font-weight: 500;
    height:100px;
    width: 500px;
    border-radius: 50px;
    text-decoration: none i !important;
    margin-top: 25px; */

   background-color: var(--Black);
   border: 2px solid var(--SkyBlue);
   color: var(--Cream);
   font-weight: 500;
   height: 60px;
   width: 200px;
   border-radius: 50px;
 }

 .bannerheading button:hover {
   border: 2px solid var(--SkyBlue);
   color: white;
   transform: scale(1.2);
   transition: all 0.8s;
 }

 .bannerheading {
   text-align: center;
   margin-top: 180px;
 }

 .bannerheading h2 {
   padding-top: 20px;
   font-weight: 700;
   padding: 10px;
   font-family: 'Anton', sans-serif;
   font-size: 70px;
   color: white;
   -webkit-text-stroke: 0.5px var(--SkyBlue);
   font-weight: 700;
 }

 .bannerheading p {
   margin-left: 90px;
   margin-right: 90px;
   font-size: 20px;
   color: white;
 }

 .bannerheading {
   font-family: 'Lobster', cursive;
   font-family: 'Source Serif Pro', serif;
   margin-top: 200px;
   text-align: center;
   text-decoration: none;

 }

 .heading {
   display: block;
   -webkit-text-stroke: 0.1px var(--Black);
   color: var(--SkyBlue);
   font-weight: 1000;
   font-size: 90px;
   font-family: 'Permanent Marker', cursive;

 }

 .loader-wrapper {
   position: fixed;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
   z-index: 1000;


 }

 .loader {
   display: block;
   position: relative;
   left: 50%;
   top: 50%;
   width: 150px;
   height: 150px;
   margin: -75px 0 0 -75px;
   border: 1px red solid;
 }